
import { useRouter } from 'next/router'
import { ReadTaleSSR } from '../../src/components/page-modules'
import { MasterLayout } from '../../src/components/layout'

const Tale = props => {
  const router = useRouter();
  const { tale } = props

  return (
    <MasterLayout>
      <h1>{tale.info.name}</h1>
      <ReadTaleSSR tale={tale}></ReadTaleSSR>
    </MasterLayout>
  );
}

Tale.getInitialProps = async (context) => {
    // console.log("router.query.id", context.query.id)
    const { id } = context.query
    const res = await import(`../../reactales/tales/${id}.json`)

    // console.log(res)

    return {
        tale: res
    }
}

export default Tale